/* Adds a grainy, scanline esque look to the contents of .container.
 * Optional flickering if your into that/really hate your users.
*/

$screen-background:#202020;

/* Simulates a CRT screen turning on */
@keyframes turn-on{
  0%{
    transform: scale(1,0.8) translate3d(0,0,0);
    filter: brightness(30);
    opacity: 1;
  }
  3.5%{
    transform: scale(1,0.8) translate3d(0,100%,0);
  }
  
  3.6%{
    transform: scale(1,0.8) translate3d(0,-100%,0);
    opacity: 1;
  } 
  
  9%{
    transform: scale(1.3,0.6) translate3d(0,100%,0);
    filter: brightness(30);
    opacity: 0.1;
  }
  
  11%{
    transform: scale(1,1) translate3d(0,0,0);
    filter: contrast(0) brightness(0);
    opacity: 0;
  }
  
  100%{
    transform: scale(1,1) translate3d(0,0,0);
    filter: contrast(1) brightness(1) saturate(1);
    opacity: 1;
  }
}

/* Flicker animation */
@keyframes flicker {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i * (1 / $steps))} {
      opacity: random(); // "random()" is pre-processed and fixed after building
    }
  }
}

.screen {
  animation: turn-on 2.5s linear;
  animation-fill-mode: forwards;
}

/* Wrapper div to apply the CRT screen effect */
.crt {
  position: relative;

  /* Flicker Effect */
  &::after{
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: transparentize($screen-background,0.9);
    opacity: 0;
    z-index: 2;
    pointer-events: none;
    animation: flicker 0.015s infinite;
  }

  /* Scanlines */
  &::before{
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(transparentize($screen-background,1) 50%, transparentize(darken($screen-background,10),0.75) 50%), linear-gradient(90deg,transparentize(#df1074,0.94),transparentize(#6e4786,0.98),transparentize(#007da5,0.94));
    z-index: 2;
    background-size: 100% 2px, 3px 100%;
    pointer-events: none;
  }
}